<script>
  import invoicePop from '@/components/invoice-pop.vue'

  export default {
    components: {
      invoicePop
    },
    data () {
      return {
        selectNum: false,
        num: 0,
        filter: {
          type: '',
        }
      }
    },
    computed: {
      invoice_piao() {
        if(this.filter.type !== '') {
          return this.$store.state.form.invoice_form.filter(v => {
            return +v.type === +this.filter.type
          })
        }
        return this.$store.state.form.invoice_form
      }
    },
    methods: {
      select (i) {
        this.selectNum = i
      },
      dbSelectPop (i) {
        this.num = i
        this.$refs.pop.private_confirm()
      }
    }
  }
</script>

